.vrf-area {
  margin: 0 0 90px;
}

.vrf-area__title {
  font-size: 40px;
  line-height: 48px;
  margin: 0 0 20px;
}

.vrf-area__desc {
  font-size: 15px;
  line-height: 23px;
  margin: 0 0 90px;
}

.vrf-area__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: -90px;
}

.vrf-area__item {
  list-style: none;
  width: calc((100% - 180px) / 3);
  margin-top: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0;

  &:not(:nth-child(3n)) {
    margin-right: 90px;
  }
}

.vrf-area__item-img {
  width: 300px;
  height: auto;
  max-width: 90%;
  margin: 0 0 50px;
}

.vrf-area__item-title {
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  margin: 0 0 10px;
  align-self: stretch;
}

.vrf-area__item-desc {
  font-size: 15px;
  line-height: 23px;
  align-self: stretch;
  margin: 0;
}

@include tablet {
  .vrf-area__item {
    width: calc((100% - 80px) / 3);

    &:not(:nth-child(3n)) {
      margin-right: 40px;
    }
  }

  .vrf-area__item-img {
    max-width: 100%;
  }
}

@include mobile {
  .vrf-area {
    margin-bottom: 60px;
  }

  .vrf-area__title {
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    margin: 0 0 20px;
  }

  .vrf-area__desc {
    text-align: center;
    margin-bottom: 30px;
  }

  .vrf-area__list {
    margin: 0;
  }

  .vrf-area__item {
    width: 100%;
    margin-top: 0;

    &:nth-child(n) {
      margin-right: 0;
    }

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  .vrf-area__item-img {
    width: 214px;
    height: auto;
  }

  .vrf-area__item-title {
    text-align: center;
  }

  .vrf-area__item-desc {
    text-align: center;
  }
}
